body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.navbar-content {
  display: -webkit-flex;
  display: flex;
  background-color: #407899;
  -webkit-align-items: center;
          align-items: center;
  height: 80px;
  padding: 0 20px 0 20px;
}

.navbar-logo {
  font-size: 25px;
  font-weight: bold;
  font-family: 'Permanent Marker', cursive;
  -webkit-flex-wrap: nowrap;
          flex-wrap: nowrap;
}

.mobile-navbar-layout > div > a {
  color: white;
  text-decoration: none;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  cursor: pointer;
}

.navbar-links > a {
  color: white;
  text-decoration: none;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  cursor: pointer;
}

.navbar-links {
  display: -webkit-flex;
  display: flex;
  color: white;
  margin-left: 40px;
}

.navbar-links > a {
  margin-right: 10px;
}

.navbar-links > a:hover {
  color: black;
  text-decoration: none;
  background-color: white;
}

@media (max-width: 615px) {
  .navbar-content {
    height: 50px;
    -webkit-flex-direction: column;
            flex-direction: column;
    /* height: 400px; */
    -webkit-align-items: flex-start;
            align-items: flex-start;
    -webkit-justify-content: center;
            justify-content: center;
  }

  .mobile-navbar-layout {
    display: -webkit-flex;
    display: flex;
    width: 100%;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: flex-start;
            align-items: flex-start;
    padding: 1px 0 0 0;
  }

  .navbar-logo {
    font-size: 18px;
  }

  .menu-open {
    height: auto;
    padding-top: 11px;
  }

  #menu-icon {
    margin-top: 2px;
  }

  .navbar-links {
    /* display: none; */
    -webkit-flex-direction: column;
            flex-direction: column;
    margin: 20px 0 0 0;
  }

  .navbar-links > a {
    margin-bottom: 20px;
  }

  .menu-button {
    background-color: white;
  }
}

.cell {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-weight: bold;
  font-size: calc(16px + (24 - 18)*(100vw - 600px)/(1200 - 600));
  background-color: tomato;
  cursor: pointer;
}

.grid > .cell:nth-child(3n+0) {
  border-right: solid rgb(104, 51, 51) 4px;
}
.grid > .cell:nth-child(9n+1) {
  border-left: solid rgb(104, 51, 51) 4px;
}

.grid > .cell:nth-child(n+0):nth-child(-n+9) {
  border-top: solid rgb(104, 51, 51) 4px;
}
.grid > .cell:nth-child(n+19):nth-child(-n+27) {
  border-bottom: solid rgb(104, 51, 51) 4px;
}

.grid > .cell:nth-child(n+46):nth-child(-n+54) {
  border-bottom: solid rgb(104, 51, 51) 4px;
}

.grid > .cell:nth-child(n+73):nth-child(-n+81) {
  border-bottom: solid rgb(104, 51, 51) 4px;
}

.available-numbers > .cell {
  background-color: rgb(63, 214, 88);
}

.row {
  display: -webkit-flex;
  display: flex;
}

.grid {
  display: grid;
  margin: 10px;
  height: 93vw;
  width: 92vw;
  max-width: 350px;
  max-height: 350px;
  grid-template-columns: repeat(9, 1fr);
  grid-template-rows: repeat(9, 1fr);
  grid-gap: 1px;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}


.wrapper {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  width: 100vw;
}

.sudoku-menu {
  display: -webkit-flex;
  display: flex;
  margin-top: 10px;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}

.selected-value {
  color: white;
}

.available-numbers {
  display: grid;
  grid-template-columns: repeat(9, 1fr);
  grid-gap: 1px;
  width: 80vw;
  max-width: 350px;
  max-height: 39px;
  height: calc(92vw/9);
  border: solid rgb(104, 51, 51) 2px;
}

.grabbed-number {
  font-size: 1.6em;
}

.invalid-number {
  color: red;
}

.s-button {
  background-color: white;
  padding: 8px;
  margin: 5px;
  cursor: pointer;
  border: rgb(83, 66, 66) solid 1px;
  border-radius: 5px;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.is-dragging {
  cursor: -webkit-grabbing;
  cursor: grabbing;
}
html {
  width: 100%;
  height: 100%;
}

body {
  height: 100%;
  background-color: #407899;
}

.App {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  height: 100vh;
}

.app-body {
  height: 100%;
  background-color: whitesmoke;
}
